html:not(.no-js){
	@if $type == "all"{
		.is-loading{
			&:after{
				$size: 48px;
				content: '';
				position: fixed;
				left: 50%;
				top: 50%;
				border: 4px solid rgba($color_main, .1);
				border-top-color: $color_main;
				width: $size;
				height: $size;
				border-radius: 50%;
				margin-top: -($size / 2);
				margin-left: -($size / 2);
				-webkit-animation: spin 1s linear infinite;
				        animation: spin 1s linear infinite;
			}
			// .page{
			// 	visibility: hidden;
			// }
		}
	}
}
body:not(.is-loading){
	@if $type == "all"{
		.page{
			animation: fadein 1s;
		}
	}
}